<template>
  <div class="usersApply h100">
    <!-- <publicCrumbs :crumbsData="crumbsData"></publicCrumbs> -->
    <div class="page-main-box">
      <KlTable
        v-bind="tableConfig"
        v-show="pageType == 'list'"
        ref="klTable"
      ></KlTable>
      <editPage
        v-if="pageType !== 'list'"
        @goBackPage="goBackPage"
        :rightBtnArr="rightBtnArr"
      >
        <addInfo
          ref="editForm"
          :pageType.sync="pageType"
          :formData="formData"
        ></addInfo>
      </editPage>
    </div>
  </div>
</template>

<script>
import addInfo from "./add";
import {
  getListApi, //获取列表
  deleteApi, //删除
  addApi, //添加
  updateApi //编辑
} from "@/api/itService/softwareApply";

export default {
  name: "softwareApply",
  components: { addInfo },
  data() {
    return {
      label: "",
      otherInfo: "",
      showScore: false,
      currentItem: {},
      tableConfig: {
        showFixedHeight: true,
        searchObj: {
          searchMethod: (params, that) => {
            if (this.tableConfig.tableDataObj) {
              this.getTableData();
            } else {
            }
          },
          searchArr: [
            {
              type: "daterange",
              label: "申请日期",
              model: "billDate"
            },
            {
              type: "el-input",
              label: "服务单号",
              model: "orderNo",
              placeholder: "服务单号"
            },
            {
              type: "el-input",
              label: "申请人",
              model: "staffunName",
              placeholder: "申请人"
            },
            {
              type: "el-select",
              label: "操作类型",
              model: "apptype",
              // id:"assetTypeId",
              option: this.$store.getters["dirtData"]("soft_type")
            }
          ]
        },
        // 查询条件参数
        queryObj: {
          method: () => {
            this.getTableData();
          },
          queryData: {
            page: 1,
            pageSize: 15 //pageCount
          }
        },
        tableDataObj: {
          tableArr: [],
          total: null
        },
        operateArr: [
          {
            name: "新建",
            // isHide: !this.$store.getters["isBtnAuth"]("insert"),
            icon: "el-icon-plus",
            method: name => {
              this.toAdd();
            }
          }
          // {
          //   name: "导出",
          //   icon: "el-icon-download",
          //   // isHide: !this.$store.getters["isBtnAuth"]("export"),
          //   method: name => {
          //     this.toExport();
          //   }
          // }
        ],
        //  table表配置参数
        tableObj: {
          // 左边选择框展示
          selectionShow: false,
          // 左边序号展示
          indexShow: true,
          // table表事件参数配置
          tableMethods: {
            selection: val => {
              this.selectionData = val;
            }
          },
          // table表展示列配置
          showItems: [
            {
              name: "服务单号",
              prop: "orderNo",
              width: 100
            },
            {
              name: "申请日期",
              prop: "billDate"
            },

            {
              name: "申请人",
              prop: "staffunName"
            },
            {
              name: "申请部门",
              prop: "depatName"
            },
            {
              name: "联系号码",
              prop: "phone"
            },
            {
              name: "软件操作类型",
              prop: "apptype",
              scopeType: "formate",
              method: row => {
                let value = row.apptype;
                return (
                  this.$store.getters["fineDirtDataName"]("soft_type", value) ||
                  value
                );
              }
            },
            // {
            //   name: "申请类型",//----缺----
            //   prop: "phone",
            // },
            {
              name: "申请描述",
              prop: "appContent"
            },
            {
              name: "备注",
              prop: "remark"
            },
            {
              name: "审批人",
              prop: "auditorName"
            },
            {
              name: "流程节点",
              prop: "stage"
            },
            {
              name: "流程状态",
              prop: "flowStatusName",
              id: "status"
            },
            {
              name: "操作",
              prop: "operate",
              width: 150,
              scopeType: "button",
              fixed: "right",
              buttonArr: [
                // 最后一行操作栏数组
                {
                  name: "详情",
                  method: row => {
                    this.detailsInfo(row);
                  },
                  isShow: (row, that) => {
                    return true;
                  },
                  icon: "false"
                },
                {
                  name: "审批",
                  method: row => {
                    //0不可以审核 1可审核
                    this.detailsInfo(row);
                  },
                  isShow: (row, that) => {
                    return row.workflowIsApprover;
                  },
                  icon: "false"
                },
                {
                  name: "编辑",
                  method: row => {
                    this.editInfo(row);
                  },
                  isShow: (row, that) => {
                    return row.status === 2;
                  },
                  icon: "false"
                },
                {
                  name: "评分",
                  method: row => {
                    this.score(row);
                  },
                  isShow: (row, that) => {
                    // return true;
                    return row.status === 1;
                  },
                  icon: "false"
                },
                {
                  name: "删除",
                  method: row => {
                    this.deleteInfo(row);
                  },
                  isShow: (row, that) => {
                    // return true;
                    return row.status === 2;
                  },
                  icon: "false"
                }
              ]
            }
          ]
        },
        // 分页展示总条配置
        paginationObj: {
          pageSizes: [15, 30, 50, 100]
        },
        paginationIsshow: true,
        showklTable: false
      },
      pageType: "list",
      formData: {}
    };
  },
  computed: {
    rightBtnArr() {
      let arr = [
        {
          text: "保存",
          icon: "el-icon-check",
          hide: this.pageType == "details",
          methods: () => {
            this.doSubmit(2);
          }
        },
        {
          text: "提交",
          icon: "el-icon-finished",
          hide: this.pageType == "details",
          methods: () => {
            this.doSubmit(1);
          }
        }
      ];
      return arr;
    }
  },
  watch: {
    // 对人员选择框做清空处理
    "tableConfig.queryObj.queryData.staffunName"(val) {
      if (val == "") {
        this.tableConfig.queryObj.queryData.empId = null;
      }
    },
    pageType(val) {
      if (val === "list") {
        this.getTableData();
      }
    }
  },

  mounted() {
    this.getTableData();
    // console.log(this.$store.getters.systemType);
  },
  methods: {
    //新建
    toAdd() {
      this.pageType = "add";
      this.formData = {};
    },
    //查询数据
    getTableData() {
      let form = Object.assign({}, this.tableConfig.queryObj.queryData);
      form.pageCount = form.pageSize;
      //日期处理
      let { billDate } = form;
      if (billDate) {
        let [beginDate, endDate] = billDate;
        form.beginDate = beginDate;
        form.endDate = endDate;
      }
      this.$delete(form, "billDate");
      this.$delete(form, "pageSize");
      this.$delete(form, "empId");
      // console.log(form);
      getListApi(form)
        .then(res => {
          // console.log(res.data);
          this.$set(
            this.tableConfig.tableDataObj,
            "tableArr",
            res.data.list || []
          );
          this.$set(
            this.tableConfig.tableDataObj,
            "total",
            res.data.total || 0
          );
        })
        .catch(res => {
          this.$set(this.tableConfig.tableDataObj, "tableArr", []);
          this.$set(this.tableConfig.tableDataObj, "total", 0);
        });
    },

    //分配
    addDistribution(data) {
      return new Promise((resolve, reject) => {
        addPortion(data)
          .then(res => {
            resolve(res);
          })
          .catch(e => {
            console.log(e);
            reject(e);
          });
      });
    },
    //详情
    detailsInfo(row) {
      this.pageType = "details";
      this.formData = Object.assign({}, row);
    },
    //编辑
    editInfo(row) {
      this.pageType = "edit";
      this.formData = Object.assign({}, row);
    },
    score(row) {
      this.pageType = "score";
      this.formData = Object.assign({}, row);
    },

    //删除
    deleteInfo(row) {
      this.$confirm(`是否确认删除`, "提示", {
        // this.$confirm(`是否删除【${row.trainingName}】`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          deleteApi(row.id).then(res => {
            this.$message.success("操作成功");
            this.getTableData();
          });
        })
        .catch(e => {});
    },
    //点击提交
    doSubmit(submitType) {
      let editFormData = null;
      this.$refs.editForm
        .getFormData()
        .then(data => {
          editFormData = data;
          // console.log(data);
          this.$refs.editForm.getTableData().then(data => {
            if (data.length) {
              // console.log(data);
              let check = true; //检查申请数量是否大于库存数量
              data.forEach(item => {
                let num = parseInt(item.instock);
                if (item.qty > num) {
                  check = false;
                }
              });
              if (check) {
                editFormData.det = data;
                const actionApi = queryData => {
                  //addApi
                  return queryData.id
                    ? updateApi(queryData)
                    : addApi(queryData);
                };

                this.$set(editFormData, "submitType", submitType);
                this.$delete(editFormData, "confirmManName");
                // this.$delete(editFormData, "assetsNumber");
                this.$delete(editFormData, "appsysName");
                this.$delete(editFormData, "assetTypeName");
                // console.log(editFormData);
                if (
                  submitType == "2" ||
                  (submitType == "1" && editFormData.confirmMan)
                ) {
                  actionApi(editFormData).then(res => {
                    this.$message.success("操作成功！");
                    if (submitType == 2) {
                      this.pageType = "edit";
                      if (res.data) {
                        this.formData.id = res.data;
                      }
                      this.$refs.editForm.getDetails(this.formData.id);
                    } else {
                      this.pageType = "list";
                    }
                  });
                } else {
                  this.$message.warning("请选择审批人！");
                }
              } else {
                this.$message.warning("申请数量不能大于库存数量！");
              }
            } else {
              this.$message.warning("请选择需申请的软件！");
            }
          });
        })
        .catch(e => {
          console.log(e);
        });
    },
    //新增
    addMethons(data) {
      if (data.id) {
        //有ID则为修改
        return new Promise((resolve, reject) => {
          updateRegist(data)
            .then(res => {
              resolve(res);
            })
            .catch(e => {
              console.log(e);
              reject(e);
            });
        });
      } else {
        //新增
        return new Promise((resolve, reject) => {
          addApi(data)
            .then(res => {
              resolve(res);
            })
            .catch(e => {
              console.log(e);
              reject(e);
            });
        });
      }
    },
    //返回
    goBackPage(data) {
      this.pageType = "list";
    },
    /**导出 */
    toExport() {
      let baseUrl = process.env.BASEURL;
      let params = "";
      let form = Object.assign({}, this.tableConfig.queryObj.queryData);
      if (form.daterange) {
        form.comeinStart = form.daterange[0] || "";
        form.comeinEnd = form.daterange[1] || "";
        delete form.daterange;
      }
      for (const key in form) {
        params += `${key}=${form[key]}&`;
      }

      params = params.substring(0, params.length - 1);
      let url = `${baseUrl}/trainRecord/exportAdmUnPass.htm?${params}`;
      window.open(url);
    }
  }
};
</script>

