<template>
  <div class="software-selete">
    <dialogPublic
      title="可选软件"
      :show.sync="showDialong"
      @doSubmit="doSave"
      @doClose="doClose"
      fullscreen
      appendTobody
    >
      <seleteTable
        :tableSetting="tableSetting"
        :hadData="hadSeleteData"
        ref="seleteTable"
      ></seleteTable>
    </dialogPublic>
  </div>
</template>

<script>
import seleteTable from "@/components/table/seleteTable";
import { getAllowListApi } from "@/api/itService/softwareApply";
export default {
  props: {
    hadSeleteData: {
      type: Array,
      default: new Array()
    },
    showSelete: {
      type: Boolean,
      default: true
    },
    // userID: {
    //   type: String,
    //   default: null
    // }
  },
  // watch: {
  //   userID(val) {
  //     if (val) {
  //       this.getAssets(val);
  //     }
  //   }
  // },
  components: { seleteTable },
  data() {
    return {
      tableSetting: {
        maxHeight: "600px",
        column: [
          {
            label: "资产分类",
            prop: "assetTypeName"
          },
          {
            label: "资产型号",
            prop: "goodsModel"
          },
          {
            label: "登记单号",
            prop: "billNo"
          },
          {
            label: "库存数量",
            prop: "instock"
          },
        ],
        data: []
      },
      showDialong: true
    };
  },
  created() {
      this.getAssets();
  },
  methods: {
    async getAssets() {
      let res = await getAllowListApi();
      let data = res.data;
      // console.log(res);
      this.$refs.seleteTable.setTableData(data || []);
    },
    doClose() {
      this.$emit("update:showSelete", false);
    },
    doSave() {
      let list = this.$refs.seleteTable.getSeleteData();
      this.$emit("changeSelete", list);
      this.doClose();
      // if (list.length == 1) {
      //   this.$emit("changeSelete", list);
      //   this.doClose();
      // } else {
      //   this.$message.warning("请选择一个需要操作的资产！");
      // }
    }
  }
};
</script>

