<template>
  <div class="record__content">
    <div class="record__baseinfo">
      <dividerTitle :title="'基本信息'"></dividerTitle>
      <editForm
        :formConfig="formConfig"
        ref="editForm"
        :formData="editFormData"
      >
        <template #assetsNumber="{editFormInfo}">
          <el-input
            placeholder="请选择资产信息"
            v-model="editFormInfo.assetsNumber"
            readonly
            :disabled="pageType == 'details' && !isWorkFlowEdit"
            size="mini"
            class="input-with-select"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click.stop="goSeleteAsset"
            ></el-button>
          </el-input>
        </template>
      </editForm>
    </div>
    <div
      class="process_score"
      v-if="editFormData.status && editFormData.status == 1"
    >
      <dividerTitle :title="'评分'"></dividerTitle>
      <processScore
        v-if="showScore"
        :scoreSetting="scoreSetting"
        :hideBtn="scoreHideBtn"
        :showScore.sync="showScore"
      ></processScore>
    </div>
    <div class="software_details">
      <dividerTitle :title="'软件申请明细'"></dividerTitle>
      <editTable
        :tableSetting="detailsSetting"
        :hideBtn="hideBtn"
        @handleClickAdd="addSoftware"
        ref="checkSoftware"
      ></editTable>
      <!-- <KlTable
        v-bind="tableConfig"
        ref="detailsTable"
      ></KlTable> -->
    </div>
    <!-- <div class="record__attach">
      <dividerTitle :title="'附件'"></dividerTitle>
      <attachTable
        :attachSetting="attachSetting"
        :hideBtn="hideBtn"
        ref="attachFile"
      ></attachTable>
    </div> -->

    <div
      class="record__workflow"
      v-if="
        pageType == 'details' &&
          editFormData.status &&
          editFormData.status !== 2
      "
    >
      <dividerTitle :title="'工作流'"></dividerTitle>
      <workFlow
        :currentFlowData="editFormData"
        :beforeSubmit="beforeSubmit"
        :otherParams="workFlowParams"
        @submitSuccess="workFlowSuccess"
      ></workFlow>
    </div>
    <softwareSelete
      v-if="showSelete"
      :hadSeleteData="hadSeleteData"
      @changeSelete="changeSeleteSorft"
      :showSelete.sync="showSelete"
    ></softwareSelete>
    <assetSelete
      v-if="showAssetSelete"
      :hadSeleteData="assetSeleteData"
      @changeSelete="changeSeleteAsset"
      :showSelete.sync="showAssetSelete"
      :userID="employeeId"
    ></assetSelete>
  </div>
</template>

<script>
import {
  getDetailsApi, //获取详情
  updateApi
} from "@/api/itService/softwareApply"; //
import softwareSelete from "./softwareSelete";
export default {
  props: {
    formData: {
      type: Object,
      default: new Object()
    },
    pageType: {
      type: String,
      default: ""
    },
    defaultSet: {
      type: Boolean,
      default: false
    }
  },
  components: { softwareSelete },
  data() {
    return {
      // userInfo: this.$store.getters.userInfo,
      hadSeleteData: [], //软件选择
      assetSeleteData: [], //资产选择
      employeeId: null,
      showSelete: false,
      showAssetSelete: false,
      editFormData: { suitableWorkArr: [] },
      trainingType: {},
      hideBtn: false,
      maxNum: 0,
      detailsSetting: {
        title: "",
        select: true,
        addMethons: true,
        maxHeight: 400,
        btnArr: ["add", "delete"], //"edit",
        column: [
          {
            label: "资产分类",
            prop: "assetTypeName",
            // required: true,
            readonly: true,
            disabled: true,
            type: "text"
          },
          {
            label: "资产型号",
            prop: "goodsModel",
            // width: 150,
            type: "text",
            readonly: true,
            disabled: true
          },
          {
            label: "登记单号",
            prop: "billNo",
            // width: 150,
            type: "text",
            readonly: true,
            disabled: true
          },
          {
            label: "申请时库存",
            prop: "instock",
            // width: 150,
            type: "number",
            min: 0,
            // required: true,
            disabled: true
            // readonly: true
          },
          {
            label: "数量",
            prop: "qty",
            // width: 150,
            type: "number",
            min: 0,
            precision: 0,
            step: 1,
            disabled: false,
            required: true
          }
        ]
      },
      formConfig: {
        itemArr: [
          {
            label: "申请日期",
            prop: "billDate",
            type: "date",
            // required: true,
            span: 8
          },
          {
            label: "申请人",
            prop: "staffunName",
            idsStr: "employeeId",
            type: "user",
            trigger: "change",
            // required: true,
            methods: obj => {
              // console.log(obj);
              let { orgNodeName, orgNodeCode, mobile, id } = obj[0];
              this.employeeId = id;
              this.$refs.editForm.setFormItem("depatName", orgNodeName);
              this.$refs.editForm.setFormItem("depatCode", orgNodeCode);
              this.$refs.editForm.setFormItem("phone", mobile);
            }
          },
          {
            label: "申请部门",
            prop: "depatName",
            id: "depatCode",
            // required: true,
            disabled: true,
            placeholder: "选择申请人后自动带出",
            type: "text"
          },
          {
            label: "联系号码",
            prop: "phone",
            // required: true,
            type: "text",
            placeholder: "选择申请人后自动带出",
            rules: {
              validator: function(rule, value, callback) {
                const reg = /^[0-9]*$/;
                if (!value) {
                  // return callback(new Error("请填写手机号码！"));
                } else if (!reg.test(value)) {
                  return callback(new Error("请填写正确的手机号码！"));
                } else {
                  callback();
                }
              },
              trigger: "change"
            }
          },
          {
            label: "资产编号",
            prop: "assetsNumber",
            type: "text",
            // required: true
          },
          {
            type: "text",
            label: "资产类型",
            prop: "assetTypeName",
            // idsStr:"assetTypeId",
            placeholder: "选择资产后自动带出",
            disabled: true
          },
          {
            label: "资产型号",
            prop: "gModel",
            type: "text",
            placeholder: "选择资产后自动带出",
            disabled: true
          },
          {
            type: "select",
            label: "申请类型", //acc_type
            prop: "apptype",
            // id: "apptype",
            // required: true,
            seleteOption: this.$store.getters["dirtData"]("soft_type"),
            formatter: val => {
              return this.$store.getters["fineDirtDataName"]("soft_type", val);
            }
          },
          {
            label: "审批人",
            prop: "confirmManName",
            idsStr: "confirmMan",
            type: "user",
            isHide: this.pageType == "details" && !this.isWorkFlowEdit,
            // required: true,
            trigger: "change"
          },
          {
            label: "申请描述",
            prop: "appContent",
            type: "textarea",
            span: 24,
            rows: 4
          },
          {
            label: "备注",
            prop: "remark",
            type: "textarea",
            span: 24,
            rows: 4
          }
        ]
      },
      procInstType:null,
      // //附件设置
      // attachSetting: {
      //   title: "",
      //   select: false,
      //   billType: this.procInstType, //页面标识
      //   sourceId: "", //单据的ID
      //   btnArr: ["add", "delete", "save"]
      // },
      //评分设置
      scoreSetting: {
        title: "",
        billType: "asset_regist", //页面标识
        sourceId: "", //单据的ID
        data: [],
        btnArr: ["save"]
      },
      scoreHideBtn: false,
      coverList: [],
      dialogVisible: false,
      activeName: "0",
      // hideBtn: false,
      showScore: false,
      beforeSubmit: {
        //工作流提交前检测
        methods: data => {
          return this.submitBefore(data);
        }
      }
    };
  },
  computed: {
    //工作流额外参数
    workFlowParams() {
      let { procInstType } = this.editFormData;
      if (this.formData.status === 4 && this.formData.workflowIsApprover) {
        return {
          procInstType: procInstType || "",
          assignercodes: [this.editFormData.confirmMan]
        };
      }
      return { procInstType: procInstType || "" };
    },
    //是否是流程中可编辑
    isWorkFlowEdit() {
      if (this.formData.status === 4 && this.formData.workflowIsApprover) {
        return true;
      } else {
        return false;
      }
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      //row.status
      // console.log(this.pageType);
      if (this.pageType == "add") {
        let defaultForm = {};
        if (this.defaultSet) {
          defaultForm = { ...this.formData };
        }
        this.editFormData = Object.assign({}, defaultForm, {
          billDate: this.$utils.getNowDay()
        });
      } else {
        this.getDetails(this.formData.id);
      }
      if (this.pageType == "details") {
        if (!this.isWorkFlowEdit) {
          this.formConfig.itemArr.map(i => (i.disabled = true));
          this.hideBtn = true;
        }
        this.hideBtn = true;
        // this.attachSetting.sourceId = this.formData.id; //附件id
      }
      if (this.pageType == "details" && this.formData.status == "4") {
        this.hideBtn = false;
      } 
      if(this.formData.status == "1"){
        this.hideBtn = true;
      }
      if (this.pageType == "score") {
        //评分
        this.scoreSetting.sourceId = this.formData.id; //评分
        this.formConfig.itemArr.map(i => (i.disabled = true));
        this.showScore = true; //显示评分
      }
    },

    //获取详情
    async getDetails(id) {
      // console.log("id: " + id);
      let res = await getDetailsApi(id);
      let obj = res.data || {};
      // this.procInstType = obj.procInstType;//附件页面标识
      // this.attachSetting.sourceId = obj.id;//附件对应的流程ID，新建流程保存后即可添加附件
      // console.log(obj);
      this.editFormData = Object.assign({}, obj, {
        confirmMan: obj.empId,
        confirmManName: obj.zystaffunName
      });
      if (obj) {
        this.employeeId = this.editFormData.employeeId;
      }

      let tableData = res.data.det;
      if (this.pageType === "edit") {
        tableData.map(i => {
          i.editable = true;
        });
      }
      this.$refs.checkSoftware.setTableData(tableData);
    },
    //提交前检测
    submitBefore(data) {
      //如果是起草节点
      if (this.isWorkFlowEdit) {
        return new Promise((resolve, reject) => {
          let editFormData = null;
          this.$refs.editForm
            .getFormData()
            .then(data => {
              editFormData = data;
              this.$set(editFormData, "submitType", 1);
              // console.log(data);
              this.editFormData = Object.assign({}, data);
              this.$refs.checkSoftware.getTableData().then(data => {
                if (data.length) {
                  let check = true; //检查申请数量是否大于库存数量
                  data.forEach(item => {
                    //遍历数组检查申请数量是否大于库存
                    let num = parseInt(item.instock);
                    if (item.qty > num) {
                      check = false;
                    }
                  });
                  if (check) {
                    editFormData.det = data;
                    this.$delete(editFormData, "confirmManName");
                    // this.$delete(editFormData, "assetsNumber");
                    this.$delete(editFormData, "appsysName");
                    this.$delete(editFormData, "assetTypeName");
                    updateApi(editFormData)
                      .then(res => {
                        resolve();
                      })
                      .catch(e => {
                        reject(e);
                      });
                  } else {
                    this.$message.warning("申请数量不能大于库存数量！");
                  }
                } else {
                  this.$message.warning("请选择需申请的软件！");
                }
              });
            })
            .catch(e => {
              console.log(e);
              reject("表单信息未填写完整");
            });
        });
      } else {
        return true;
      }
    },
    //工作流提交成功之后
    workFlowSuccess() {
      this.$emit("update:pageType", "list");
    },
    //获取表单数据
    getFormData() {
      return this.$refs.editForm.getFormData();
    },
    getTableData() {
      return this.$refs.checkSoftware.getTableData();
    },
    //点击新增软件
    addSoftware(data, hadData) {
      this.showSelete = true;
    },
    //确定选择软件
    changeSeleteSorft(list) {
      let [obj] = list;
      list.map(i => {
        i.qty = 1;
        i.sourceId = i.id;
        i.editable = true;
        this.$delete(i, "id");
      });
      // console.log(list);
      this.$refs.checkSoftware.setTableData(list || []);
    },
    //点击选择资产
    goSeleteAsset() {
      // console.log("diaoyong");
      if (this.employeeId) {
        if (this.pageType == "details") {
          return;
        }
        let sourceId = this.$refs.editForm.getFormItem("sourceId");
        this.assetSeleteData = [{ id: sourceId }];

        this.showAssetSelete = true;
      } else {
        this.$message.warning("请先选择申请人");
      }
    },
    //确定选择资产
    changeSeleteAsset(list) {
      // console.log(list);
      let [obj] = list;
      let { assetsNumber, assetTypeName, assetTypeId, goodsModel, id } = obj; //assetsNumber
      this.$refs.editForm.setFormItem("assetsNumber", assetsNumber);
      this.$refs.editForm.setFormItem("assetTypeName", assetTypeName);
      this.$refs.editForm.setFormItem("assetsId", id);
      this.$refs.editForm.setFormItem("gModel", goodsModel);
      // this.$refs.editForm.setFormItem("sourceId", sourceId);
    }
  }
};
</script>

<style scoped lang="scss">
</style>