import request from '@/request/api'
/**===========基本信息接口 */

// 获取软件申请
export function getListApi(data = {}) {
    return request({
        url: '/workorder/OfficeappAction/queryList.htm',
        method: 'post',
        data: data
    })
}
// 获取可申请软件列表
export function getAllowListApi(data = {}) {
    return request({
        url: '/workorder/OfficeappAction/queryRegist.htm',
        method: 'post',
        data: data
    })
}

//详情
export function getDetailsApi(id = "") {
    return request({
        url: '/workorder/OfficeappAction/detail.htm',
        method: 'post',
        params:{ id: id }
    })
}
//删除
export function deleteApi(id = "") {
    return request({
        url: '/workorder/OfficeappAction/delete.htm',
        method: 'post',
        params: { id: id }
    })
}
//提交流程
export function addApi(data = {}) {
    return request({
        url: '/workorder/OfficeappAction/addOfficeapp.htm',
        method: 'post',
        data: data
    })
}
//保存草稿
export function updateApi(data = {}) {
    return request({
        url: '/workorder/OfficeappAction/updateOfficeapp.htm',
        method: 'post',
        data: data
    })
}